@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap");
/*Variables à modifier*/
.Card {
  z-index: 4;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  background: linear-gradient(to bottom right, #03222E, #0085C2);
  cursor: pointer;
  box-shadow: 5px 5px 20px black;
  width: 90%;
  height: none;
}
@media all and (min-width: 992px) {
  .Card {
    margin-top: none;
    margin-bottom: none;
    margin-left: none;
    margin-right: none;
  }
}
@media all and (min-width: 768px) and (max-width: 991px) {
  .Card {
    margin-top: none;
    margin-bottom: none;
    margin-left: none;
    margin-right: none;
  }
}
@media all and (max-width: 767px) {
  .Card {
    margin-top: none;
    margin-bottom: none;
    margin-left: 4%;
    margin-right: 4%;
  }
}

.Card-image-border {
  overflow: hidden;
  border-radius: 10px;
}
@media all and (min-width: 992px) {
  .Card-image-border {
    width: 60%;
    height: 300px;
  }
}
@media all and (min-width: 768px) and (max-width: 991px) {
  .Card-image-border {
    width: 60%;
    height: 250px;
  }
}
@media all and (max-width: 767px) {
  .Card-image-border {
    width: 60%;
    height: 250px;
  }
}

.Card-image {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media all and (min-width: 992px) {
  .Card-image {
    width: 200px;
    height: none;
  }
}
@media all and (min-width: 768px) and (max-width: 991px) {
  .Card-image {
    width: 170px;
    height: none;
  }
}
@media all and (max-width: 767px) {
  .Card-image {
    width: 170px;
    height: none;
  }
}

.Card-text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  text-align: left;
}
@media all and (min-width: 992px) {
  .Card-text {
    width: 45%;
    height: 270px;
  }
}
@media all and (min-width: 768px) and (max-width: 991px) {
  .Card-text {
    width: 45%;
    height: 220px;
  }
}
@media all and (max-width: 767px) {
  .Card-text {
    width: 45%;
    height: 220px;
  }
}

.black-dust {
  width: 30px;
  height: auto;
}

.black-dust-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.heart {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 90%;
  height: none;
}

.Card-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: none;
  height: 170px;
  margin-top: none;
  margin-bottom: 10px;
  margin-left: none;
  margin-right: none;
}

.Card-title1 {
  font-family: "EB Garamond", serif;
  font-size: 24px;
  font-weight: normal;
  color: white;
}

.Card-title2 {
  font-family: "EB Garamond", serif;
  font-size: 18px;
  font-weight: normal;
  color: white;
}

/*Partie à modifier lors du travail sur les cartes*/
.card-logo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.card-logo_dimensions {
  width: 55px;
  height: 55px;
}
.card-logo_aspect {
  font-size: 24px;
  font-style: normal;
  color: black;
}

.animation-coeur {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.animation-coeur:hover > .coeur1 {
  opacity: 1;
}
.animation-coeur:hover > .coeur2 {
  opacity: 0;
}

.coeur1 {
  position: absolute;
  z-index: 2;
  font-size: 24px;
  background: linear-gradient(#9356dc, #ff79da);
  -webkit-background-clip: text;
          background-clip: text; /* Couleur du texte, ici le logo */
  color: transparent; /* le texte, ici le logo, n'est plus noir mais transparent pour laisser place à l'autre couleur */
  opacity: 0;
  transition: all 550ms;
}

.coeur2 {
  position: absolute;
  z-index: 1;
  font-size: 22px;
  color: black;
  opacity: 1;
  transition: all 550ms;
}/*# sourceMappingURL=Card.css.map */